html {
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  margin: 0px;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
    "Helvetica Neue", sans-serif;
  font-size: 22px;
  -webkit-font-smoothing: antialiased;
  line-height: 1.3;
  text-align: center;
  transition: all 0.4s cubic-bezier(0.3, 0.8, 0.2, 1) 0s;
}
a {
   outline: 0;
}
mark {
  background-color: #fdcc36;
  color: black;
}
.App {
  a {
    text-decoration: none;
  }

  h1 {
    font-size: 50px;
    line-height: 1.2;
    font-weight: bold;
    margin: 10px 0px;
  }

  h2 {
    font-size: 50px;
    line-height: 1.3;
    font-weight: bold;
    margin: 10px 0px;
  }

  h3 {
    font-size: 40px;
    line-height: normal;
    font-weight: bold;
    margin: 10px 0px;
  }

  h4 {
    font-size: 30px;
    line-height: 1.33;
    font-weight: bold;
    margin: 10px 0px;
  }

  h5 {
    font-size: 27px;
    line-height: 1.50;
    font-weight: normal;
    margin: 10px 0px;
  }

  h6 {
    font-size: 15px;
    font-weight: 900;
    letter-spacing: 3.4px;
    margin: 10px 0px;
  }

  p {
    font-size: 22px;
    line-height: 1.64;
    font-weight: normal;
    margin: 10px 0px;
  }
  .Lighten {
    opacity: 0.7;
  }

  .pages__Logo {
    display: grid;
    grid-template-columns: repeat(5, auto);
    gap: 20px;
    justify-items: center;
    margin: 30px 0px 0px;
  }

  .HeroSection-container {
    margin: 70px 0px;

    .text-container {
      max-width: 700px;
      margin: 0px auto;
      display: grid;
      grid-template-columns: 1fr;
      justify-items: center;
    }

    .image-container {
      margin: 0px auto;
      max-width: 1000px;
      padding: 60px 0px 0px;
    }
  }
  .AppStoreButton__Group{
      img{
        width: 170px;
      }
  }

  .DarkModeToggle {
    position: relative;
    cursor: pointer;
    width: 28px;
    height: 28px;

    .icon {
      position: absolute;
      left: 0px;
      top: 0px;
      width: 100%;
      height: 100%;
      transition: opacity 0.3s cubic-bezier(0.2, 0.8, 0.2, 1) 0s,
        transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.2) 0s;
      opacity: 0;
    }
  }

  .AppStoreButton__Group {
    opacity: 0;
    animation: 3s cubic-bezier(0.2, 0.8, 0.2, 1) 0.3s 1 normal forwards running HeroAnimation;
    margin: 20px 0px;

    &:hover {
      transform: translateY(-3px);
      cursor: pointer;
    }
  }

  .Constraints-container {
    margin: 0px auto;
    padding: 100px 10px 0px;
    max-width: 1000px;
  }

  .Card__Styles-container {
    width: 455px;
    padding: 40px;
    border-radius: 20px;
    text-align: left;
    transition: all 0.4s cubic-bezier(0.3, 0.8, 0.2, 1) 0s;

    h4 {
      font-size: 22px;
    }

    p {
      font-size: 20px;
    }

    .light {
      opacity: 0.3;
    }
  }

  .AppStoreReviews__CardsGrid-container {
    margin: 0px auto;
    display: grid;
    max-width: 930px;
    grid-template-columns: auto auto;
    gap: 20px;
    justify-items: center;
  }

  .Constraint {
    max-width: 1000px;
    margin: 0px auto;
    padding: 20px 0px 0px;
  }
  .Containering {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -moz-flex;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-basis: 2;
  }
  .ContaineringColumn {
    width: 300px;
    border-radius: 45px;
    padding: 20px;
    transition: 0.3s;
    transition-timing-function: ease;
  }
  .ContaineringColumn:hover {
      transform: scale(1.03);
  }

  .Bottom-Text {
    font-size: 20px;
    font-weight: normal;
    opacity: 0.7;
  }
//   @media ( max-width: 800px ) {
//     .Containering {
//            flex-basis: calc(50% - 20px);
//            margin: 10px;
//          }
// }

  .HeroSection-container {
    margin: 0px;
    padding: 40px 0px;

    .text-container {
      max-width: 500px;
      margin: 0px auto;
      display: grid;
      grid-template-columns: 1fr;
      justify-items: center;

      h1 {
        opacity: 0;
        animation: 3s cubic-bezier(0.2, 0.8, 0.2, 1) 0.1s 1 normal forwards running HeroAnimation;
      }

      h5 {
        opacity: 0;
        animation: 3s cubic-bezier(0.2, 0.8, 0.2, 1) 0.2s 1 normal forwards running HeroAnimation;
      }
    }

    .button {
      opacity: 0;
      animation: 3s cubic-bezier(0.2, 0.8, 0.2, 1) 0.3s 1 normal forwards running HeroAnimation;
    }
  }
  .footer-container {
    .container {
      font-size: 17px;
      letter-spacing: -0.4px;
      padding: 10px;

      .tagline {
        font-weight: bold;
        padding: 30px;
      }

      .posts {
        opacity: 0.7;
        transition: all 0.2s ease 0s;

        &:hover {
          opacity: 1;
        }
      }

      .feedback {
        opacity: 0.7;
        transition: all 0.2s ease 0s;

        &:hover {
          opacity: 1;
        }
      }
    }
  }

  .header-container {
    .Header {
      box-sizing: border-box;
      position: fixed;
      width: 100%;
      padding: 40px 20px;
      z-index: 100;
      -webkit-transition: 0.4s;
      transition: 0.4s;
      transition-timing-function: ease;

      .LogoGroup {
        display: grid;
        grid-template-columns: auto auto;
        -webkit-box-align: center;
        align-items: center;
        gap: 10px;
        justify-self: start;

        .LogoText {
          font-size: 26px;
          font-weight: 700;
          letter-spacing: -0.3px;
          font-family: avenir next;
        }
      }
    }

    .HeaderScrolled {
      padding: 12px 20px;
      backdrop-filter: blur(20px);
    }

    .Header .LogoMark {
      width: 30px;
      height: 50px;
      -webkit-transition: 0.4s;
      transition: 0.4s;
      transition-timing-function: ease;
      fill: red;
      transform: scale(-1, 1);
    }

    .HeaderScrolled .LogoMark {
      width: 30px;
      height: 40px;
    }

    .HeaderGroup {
      display: grid;
      margin: 0px auto;
      grid-template-columns: 1fr auto auto;
      -webkit-box-align: center;
      align-items: center;
      max-width: 950px;
    }

    .Header {
      .ButtonGroup {
        justify-self: end;
      }

      button {
        padding: 8px 19px;
        background-image: linear-gradient(280deg, #be8b3b, #e2bc1d);
        background-size: cover;
        font-size: 17px;
        color: white;
        font-weight: 700;
        border-radius: 4px;
        outline: none;
        border: none;
        cursor: pointer;
        -webkit-transition: 0.2s;
        transition: 0.2s;
        transition-timing-function: ease;

        &:hover {
          transform: scale(0.97);
        }
      }

      .LogoGroup {
        -webkit-transition: 0.2s;
        transition: 0.2s;
        transition-timing-function: ease;
        &:hover {
          transform: scale(0.97);
        }
      }
    }
  }
}
//Help Style
.Constraints-container-help-wrap{
    max-width: 1040px;
    margin: 0px auto;
    padding: 100px 20px 0px;
}
.page-breadcrumb{
    box-sizing: border-box;
    width: 100%;
    border-bottom: 1px solid rgb(228, 228, 228);
    padding: 30px 0px;
    h3{
      font-weight: 800;
      letter-spacing: -0.8px;
      text-align: left;
    }
}
.help-container{
    display: grid;
    grid-template-columns: 6fr 4fr;
    cursor: pointer;
    gap: 30px;
    padding: 30px 0px;
    border-bottom: 1px solid rgb(228, 228, 228);
    -webkit-transition: 0.2s;
    transition: 0.2s;
    transition-timing-function: ease;
    &:hover{
      transform: scale(0.99);
    }
    .col1{
      max-width: 500px;
    }
    .col1,
    .col2{
      align-self: center;
    }
    h6{
      font-size: 12px;
      text-align: left;
      text-transform: uppercase;
    }
    h5{
      font-weight: normal;
      font-size: 22px;
      text-align: left;
      margin: 0px;
    }
    p{
      font-size: 18px;
      opacity: 0.5;
      text-align: left;
      margin: 0px;
    }
    img{
      max-width: 385px;
      border-radius: 6px;
    }
}
.help__Contact{
  padding: 50px 0px 100px;

  h5{
    font-weight: normal;
    font-size: 22px;
    text-align: left;
    margin: 0px;
  }
  p{
    font-size: 18px;
    opacity: 0.5;
    text-align: left;
    margin: 0px;
  }
  .help__ButtonSection{
    display: flex;
    grid-template-columns: auto auto;
    margin: 20px 0px;
  }
  .help__RoundButton{
    cursor: pointer;
    width: 190px;
    font-size: 15px;
    font-weight: bold;
    color: rgb(11, 132, 255);
    margin: 0px 20px 0px 0px;
    outline: none;
    border-width: initial;
    border-style: none;
    border-color: initial;
    border-image: initial;
    padding: 10px;
    border-radius: 10px;
    background: rgb(240, 240, 248);
    transition: scale 1s cubic-bezier(0.2, 0.8, 0.2, 1) 0s;
    &:hover{
      transform: scale(0.98);
    }
  }
}

//Screenshots Style
.policies-page-wrap{
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    display: flex;
    flex-direction: column;
    align-content: center;
    padding-top: 80px;
    font-size: 24px;
    line-height: 42px;
    margin: 0px 20px;
}

.policies-page{
    display: grid;
    grid-template-columns: 1fr;
    text-align: center;
    .laptop-image {
      img{
        max-width: 100%;
      }

    }
}
.text-section {
  max-width: 700px;
  margin: 0px auto;
  .title{
    font-size: 42px;
  }
}
.policies-page-col{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 30px 0px;
    .text-section {
      align-self: center;
      justify-self: center;
      max-width: 454px;
      padding: 0px 20px 0px 0px;
      .caption {
        font-size: 32px;
        color: rgb(176, 176, 176);
        margin: 0px;
      }
      .title {
        font-size: 32px;
        margin: 0px;
      }
      .body {
        margin: 20px 0px 0px;
      }
  }
  .img-section{
    img{
      max-width: 100%;
    }
  }
}
